import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { Config, Api } from './../_helpers/index';
import { AuthenticationService } from './../_services/index';

@Injectable()
export class ResetService {
    headers: Headers;
    options: RequestOptions;
    public url: string;
    constructor(private http: Http, private authenticationService: AuthenticationService) {
        this.url = Api.APP_PATH;
    }
    private _serverError(err: any) {

        if (err instanceof Response) {
            return Observable.throw(JSON.parse(JSON.stringify(err || null)) || 'backend server error');
        }
        return Observable.throw(err || 'backend server error');
    }

    //  reset(email: string): Observable<boolean> {
    reset(email: string): Observable<boolean> {
         let headers = new Headers();
        headers.append("Content-Type", "application/json; charset=utf8");
        headers.append("Authorization", this.authenticationService.token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.url + 'users/sendReset', JSON.stringify({ username: email }), options)
            .map((response: Response) => {
                return true;
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }
    //change old password with new password
    change(password: string, username: string, code?: any): Observable<boolean> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json; charset=utf8");
        headers.append("Authorization", this.authenticationService.token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.url + 'users/resetPwd', JSON.stringify({ newPassword: password, username: "reda.elamrani@soprabanking.com", passwordToken: code }), options)
            .map((response: Response) => {
                return true;
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }
}


