import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { CommonModule, NgSwitch } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { LoadersCssModule } from 'angular2-loaders-css';
import { RibInputComponent } from './rib-input/rib-input.component';
import { CanInputComponent } from './can-input/can-input.component';
import { IntlInputTelComponent } from './intl-input-tel/intl-input-tel.component';
import { AmountInputComponent } from './amount-input/amount-input.component';
import { CurrencyMaskPipe } from './amount-input/pipe-currency-mask.pipe';
import { CurrencyFormatterDirective } from './amount-input/pipe-currency-mask.directive';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ReCaptchaModule } from 'angular2-recaptcha';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { RibNumberPipe } from "./rib-input/rib-account-number.pipe";
import { RibNumberFormatterDirective } from "./rib-input/rib-card-number.directive";

import { CanNumberPipe } from "./can-input/can-account-number.pipe";
import { CanNumberFormatterDirective } from "./can-input/can-card-number.directive";
import { NgxIntlTelInputModule } from 'ngx-intl-tel-input';
import { SafeHtmlPipe } from './safe.html.pipe';
import { PopoverModule } from 'ngx-bootstrap/popover';
import { CountryCode } from './../_helpers/index';
import { TabsModule } from 'ngx-bootstrap/tabs';

@NgModule({
  imports: [
    HttpModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    NgxIntlTelInputModule,
    ReCaptchaModule,
    NgbModule.forRoot(),
    TabsModule.forRoot(),
    PopoverModule.forRoot(),
    LoadersCssModule,
    TranslateModule.forChild({})
  ],
  declarations: [RibInputComponent, IntlInputTelComponent, CanInputComponent,CanNumberPipe, CanNumberFormatterDirective, AmountInputComponent,CurrencyFormatterDirective, RibNumberFormatterDirective, CurrencyMaskPipe, RibNumberPipe, SafeHtmlPipe],
  providers: [RibNumberPipe,CanNumberPipe, CurrencyMaskPipe],
  exports: [
    CommonModule,
    NgSwitch,
    SafeHtmlPipe,
    IntlInputTelComponent,
    CanNumberPipe,
    CanNumberFormatterDirective,
    CanInputComponent,
    TabsModule,
    CurrencyMaskPipe,
    NgxIntlTelInputModule,
    RibNumberFormatterDirective,
    CurrencyFormatterDirective,
    RibNumberPipe,
    AmountInputComponent,
    TranslateModule,
    NgbModule,
    PopoverModule,
    LoadersCssModule,
    ReactiveFormsModule,
    ReCaptchaModule,
    FormsModule,
    RibInputComponent
  ]
})
export class SharedLazyModule { }



