import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DatePipe } from '@angular/common';
import { OffersService } from './../../../_services/index';
import { TranslateModule, TranslateLoader, TranslateService } from '@ngx-translate/core';
declare var $: any;
@Component({
  moduleId: module.id,
  templateUrl: 'offers.component.html',
  providers: []
})

export class OffersComponent implements OnInit {
  error = '';
  offers: any = {};
  constructor(private router: Router, private offersService: OffersService, private translate: TranslateService) { }

  ngOnInit() {
    $('.slick-container').slick({
      dots: true,
      infinite: true,
      arrows: true,
      speed: 500,
      fade: true,
      cssEase: 'linear',
      autoplay: true,
      autoplaySpeed: 3000,
      slidesToShow: 1,
      adaptiveHeight: true
    });
    this.getOffers();
  }
  getOffers() {
    let lang = this.translate.currentLang;
    let date = new Date();
    var datePipe = new DatePipe('en-US')
    let newDate = datePipe.transform(date, 'dd/MM/yyyy');
    this.offersService.getOffers({
      companyCode: 0,
      channelCode: 0,
      language: lang,
      date: newDate
    })
      .subscribe(result => {
        let offers = result.values != null ? result.values : null;
        if (offers != null) {
          this.offers = offers;
        }

      }, err => {
        if (<any>err.error) {
          switch (err.error.status) {
            case 403: {
              this.error = 'you dont have acces to this area';
            }
            case 500: {
              this.error = 'backend server error';
            }
            case 404: {
              this.error = 'Not Found';
            }
            default: {
              this.error = 'backend server error';
            }
          }
        }
      });
  }
  activate() {
    this.router.navigate(['/activate/account']);
  }
  login() {
    this.router.navigate(['index/login']);
  }
}


