import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { Config, Api } from './../_helpers/index';

@Injectable()
export class OffersService {
    public token: string;
    headers: Headers;
    options: RequestOptions;
    public url: string;
    constructor(private http: Http) {
        this.url = Api.APP_PARAM_PATH;
    }
    private _serverError(err: any) {
        if (err instanceof Response) {
          return Observable.throw(JSON.parse(JSON.stringify(err || null )) || 'backend server error');
        }
        return Observable.throw(err || 'backend server error');
    }

    getOffers(params: any): Observable<any> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json; charset=utf8");
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.url + 'entities/Offre', JSON.stringify(params), options)
            .map((response: Response) => {
                 return response.json();
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }

    getIndicatif(params: any): Observable<any> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json; charset=utf8");
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.url + 'entities/Indicatif', JSON.stringify(params), options)
            .map((response: Response) => {
                 return response.json();
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }
}


