import { Component, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'can-input',
  templateUrl: './can-input.component.html',
  providers: []
})
export class CanInputComponent {

  public mask = [ /[1-9]/, /\d/, /\d/, ' ', /\d/, /\d/, /\d/, ' ', /\d/, /\d/, /\d/,' ', /\d/, /\d/, /\d/,' ', /\d/, /\d/, /\d/,' ', /\d/, /\d/, /\d/,' ', /\d/, /\d/, /\d/]
  @Input('label')
  public label: any;
  @Input('group')
  public form: FormGroup;
  cardLength: boolean = true;
   constructor() { }

  onKey(event: any) { // without type info
    this.cardLength = false;
  }
  onPress(event: any) { // without type info
    this.cardLength = true;
  }
}



