import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { Config, Api } from './../_helpers/index';

@Injectable()
export class AuthenticationService {
    public token: string;
    headers: Headers;
    options: RequestOptions;
    public url: string;
    constructor(private http: Http) {
        // set token if saved in local storage
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.token = currentUser && currentUser.token;
        this.url = Api.APP_PATH;
    }
    private _serverError(err: any) {
        if (err instanceof Response) {
            return Observable.throw(JSON.parse(JSON.stringify(err || null)) || 'backend server error');
        }
        return Observable.throw(err || 'backend server error');
    }

    //  login(username: string, password: string): Observable<boolean> {
    login(username: string, password: string): Observable<boolean> {
        return this.http.post(this.url + 'login', JSON.stringify({ username: username, password: password }))
            .map((response: Response) => {
                // login successful if there's a jwt token in the response
                let token = response.headers.get('Authorization');
                if (token) {
                    // set token property
                    this.token = token;
                    var currentUser: any = JSON.parse(localStorage.getItem('currentUser'));
                    var country = currentUser != null ? currentUser.country : null;
                    currentUser = {
                        country: country,
                        username: username,
                        token: token
                    };
                    // store username and jwt token in local storage to keep user logged in between page refreshes
                    localStorage.setItem('currentUser', JSON.stringify(currentUser));
                    // return true to indicate successful login
                    return true;
                } else {
                    // return false to indicate failed login
                    return false;
                }
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }

    logout(): void {
        // clear token remove user from local storage to log user out
        this.token = null;
        localStorage.removeItem('currentUser');
    }
}


