import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateModule, TranslateLoader, TranslateService } from '@ngx-translate/core';
import { AuthenticationService, UserService } from './../../../../_services/index';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PlatformLocation } from '@angular/common';

@Component({
  selector: 'login',
  moduleId: module.id,
  templateUrl: 'login.component.html',
  providers: []
})

export class LoginComponent implements OnInit {
  model: any = {};
  loading = false;
  error = '';
  title: String;
  description: String;
  country: string;
  form: FormGroup;

  constructor(
    private router: Router,
    public translate: TranslateService,
    private userService: UserService,
    private fb: FormBuilder,
    location: PlatformLocation,
    private authenticationService: AuthenticationService
  ) {
// location.onPopState(() => {

//         alert('pressed back!');

//     });
   }


  ngLanguageInit() {
    this.translate.use(this.translate.currentLang);
  }
  ngOnInit() {
    if (localStorage.getItem('currentUser')) {
      var currentUser = JSON.parse(localStorage.getItem('currentUser'));
      // logged in so return true
      if (currentUser.hasOwnProperty('token')) {
        if (currentUser.token !== null) {
          this.router.navigate(['dashboard/transfer']);
        }

      }
    }
    this.initForm();
    this.ngLanguageInit();
  }

  initForm() {
    this.form = this.fb.group({
      email: ['', [Validators.required, Validators.pattern(/^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i)]],
      password: ['', [Validators.required]],
    });
  }

  reset() {
    this.router.navigate(['/password_reset']);
  }



  login() {
    this.loading = true;
    this.router.navigate(["/dashboard/transfer"])
    // this.authenticationService.login(this.form.value.email, this.form.value.password)
    //   .subscribe(result => {
    //     if (result === true) {
    //       this.getUser();
    //     } else {
    //       // login failed
    //       this.error = 'Username or password is incorrect';
    //       this.loading = false;
    //     }
    //   }, err => {
    //     if (<any>err.error) {
    //       switch (err.error.status) {
    //         case 403: {
    //           this.error = 'Mot de passe ou e-mail incorrect';
    //           return;
    //         }
    //         case 500: {
    //           this.error = 'Le site rencontre un problme';
    //           return;
    //         }
    //         case 404: {
    //           this.error = 'Mot de passe ou e-mail incorrect';
    //           return;
    //         }
    //         default: {
    //           this.error = 'Le site rencontre un problme';
    //           return;
    //         }
    //       }
    //     }
    //     // login failed
    //     this.loading = false;
    //   });
  }

  getUser() {
    this.userService.getUser()
      .subscribe(result => {
        if (result.forceChangePwd === true) {
          this.router.navigate(["/password_change"])
        } else {
          this.router.navigate(["/dashboard/transfer"])
        }
      }, err => {
        if (<any>err.error) {
          switch (err.error.status) {
            case 403: {
              this.error = 'Mot de passe ou e-mail incorrect';
            }
            case 500: {
              this.error = 'Le site rencontre un problme';
            }
            case 404: {
              this.error = 'Mot de passe ou e-mail incorrect';
            }
            default: {
              this.error = 'Le site rencontre un problme';
            }
          }
        }
        // login failed
        this.loading = false;
      });
  }
}


