import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { Config, Api } from './../_helpers/index';
import { AuthenticationService } from './../_services/index';

@Injectable()
export class ActivateAccountService {
    public token: string;
    headers: Headers;
    options: RequestOptions;
    public url: string;
    constructor(private http: Http, private authenticationService: AuthenticationService) {
        // set token if saved in local storage
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.token = currentUser && currentUser.token;
        this.url = Api.APP_PATH;
    }
    private _serverError(err: any) {
        if(err instanceof Response) {
          return Observable.throw(JSON.parse(JSON.stringify(err || null )) || 'backend server error');
        }
        return Observable.throw(err || 'backend server error');
    }

    identifyUser(params: any): Observable<any> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json; charset=utf8");
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.url + 'activation/customers/identify', JSON.stringify(params), options)
            .map((response: Response) => {
                 return response.json();
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }

    activateUser(params: any): Observable<any> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json; charset=utf8");
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.url + 'activation/customers/acitvationLink', JSON.stringify(params), options)
            .map((response: Response) => {
 
                 return response.json();
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }

    //change old password with new password
    changePwd(oldPpassword: string,password: string, username: string, code?: any): Observable<boolean> {
        let headers = new Headers();
        headers.append("Content-Type", "application/json; charset=utf8");
        headers.append("Authorization", this.authenticationService.token);
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.url + 'users/resetPwd', JSON.stringify({oldPpassword: oldPpassword, newPassword: password, username: "reda.elamrani@soprabanking.com", passwordToken: code }), options)
            .map((response: Response) => {
                return true;
            })
            .catch((error: any) => Observable.throw(this._serverError(error))); //...errors if any
        ;
    }
}


